import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import bg from '../assets/contacts_bg.jpg';
import {contacts} from '../data/data';
import { Footer } from './Footer';
import map from '../assets/map.png';

const useStyles = makeStyles(theme => ({
    contacts: {
        position: 'relative',
        height: '100vh',        
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        paddingTop: '50px',
        backgroundImage: `url(${bg})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        color: '#c2c2c2',
        fontFamily: 'Nunito',
        [theme.breakpoints.down('md')]: {            
            paddingBottom: '20px',
            height: '100%'
        }
    },
    title: {
        fontSize: '2rem'       
    },
    container: {     
        paddingTop: '20px',
        height: '80%',       
        display: 'flex',
        width: '80%',
        justifyContent: 'space-around',
        alignItems: "center",
        [theme.breakpoints.between('xs', 'sm')]: {
            flexDirection: 'column',           
        }

    },
    contactsItem: {
        width: '50%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        marginBottom: '50px'
    },
    itemsCont: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center'
    },
    itemIcon: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '50px',
        height: '50px',
        borderRadius: '50%',
        backgroundColor: '#c2c2c2',
        [theme.breakpoints.between('xs', 'sm')]: {
            width: '80px',
            height: '80px',
            fontSize: '10px'
        }
    },
    itemText: {
        width: '240px',
        height: '40px',
        textAlign: 'center',
        marginTop: '30px',
        fontSize: '1rem',
        [theme.breakpoints.between('xs', 'sm')]: {
            marginTop: '10px',
            fontSize: '1rem',
            width: '150px',
        }
    },
    map: {       
        alignSelf: 'flex-start',
        width: '500px',   
        height: '390px',
        border: '5px solid #fff',
        borderRadius: '10px',       
        [theme.breakpoints.between('xs', 'sm')]: {
            display: 'none'
        },
        [theme.breakpoints.up('1367')]: {
            alignSelf: 'center',
        }
        
    },
    mapPic: {
        width: '100%',
        height: '100%'
    }
}))

export const Contacts = () => {
    const s = useStyles();
    return (
        <div className={s.contacts} id='contacts'>
            <h1 className={s.title}>Наши контакты</h1>
            <div className={s.container}>
                <div className={s.map}>
                    <img className={s.mapPic} src={map} alt='map' />
                </div>
                <div className={s.itemsCont}>
                    {contacts.map(({
                    icon,
                    text
                }, index) => {                  
                    return (
                        <div key={index} className={s.contactsItem}>
                            <div className={s.itemIcon}>
                                {icon}
                            </div>
                            <div className={s.itemText}>
                                {text}
                            </div>
                        </div>
                    )
                })}
                </div>
                
            </div>
            <Footer />
        </div>
    )
}