import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import {Fade} from '@material-ui/core';
import useWindowPosition from '../hooks/useWindowPosition';

const useStyles = makeStyles((theme) => ({
    about: {
        height: '100vh',
        backgroundColor: '#eff2f1',       
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        borderBottom: '3px solid #fe8d12',
        [theme.breakpoints.down('md')]: {
            flexDirection: 'column',
            height: '100%',
            padding: '20px 0 100px'
        }
    },
    aboutPicCont: {
        width: '500px',
        [theme.breakpoints.between('xs', 'sm')]: {
            width: '80%'
        }
    },
    aboutPic: {
        width: '100%'
    },
    body: {
        width: '33%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        marginLeft: '40px',
        textIndent: '1rem',
        [theme.breakpoints.down('md')]: {
            width: '80%',
            marginLeft: '0',
            
        }
    },
    firstTitle: {
        fontFamily: 'Nunito',
        display: 'none',        
        [theme.breakpoints.between('xs', 'sm')]: {
            display: 'block',
            fontSize: '1.75rem',
        }
    },
    secondTitle: {
        fontFamily: 'Nunito',
        display: 'blcok',
        fontSize: '1.75rem',
        [theme.breakpoints.between('xs', 'sm')]: {
            display: 'none'            
        }
    },
    description: {
        paddingTop: '20px',
        textAlign: 'justify'
    }

}));

export const About = () => {
    const s = useStyles();
    const checked = useWindowPosition('about');
    return (
        <div className={s.about} id='about'>
         <h2 className={s.firstTitle}>О нас</h2>
            <Fade
                className={s.aboutPicCont}
                in={checked}
                {...(checked ? { timeout: 1500 } : {})}>
                <img
                    className={s.aboutPic}
                    src='https://ownwoodenhouse.com/img/misc/2018122718060630.jpg?ver=156446863414'
                    alt='about-pic'/>
            </Fade>
            <Fade in={checked} {...(checked ? { timeout: 1500 } : {})}>
                <div className={s.body}>
                    <h2 className={s.secondTitle}>О нас</h2>
                    <div className={s.description}>
                        Aute anim fugiat labore occaecat fugiat. Sit consequat pariatur mollit velit
                        veniam magna cillum. Lorem dolor deserunt sint amet elit veniam. Officia laboris
                        cupidatat occaecat ullamco fugiat eu nisi laboris enim aute non labore. Proident
                        reprehenderit eu exercitation dolor eiusmod ipsum proident eu ut excepteur.
                        Mollit fugiat elit nostrud adipisicing culpa consequat ut irure officia labore
                        qui enim eu velit.
                    </div>
                </div>
            </Fade>

        </div>
    )
}